import React,{Component} from "react";
import Pizzalist from "./pizzalist";
import Pizzaconf from "./pizzaconf";
import pizzalist from '../Jsons/pizzalist.json';

class PizzaListConfig extends  Component{
    constructor(props) {
        super(props);
        this.state = {
            configuring : false,
            pizzaConfElement : null,
            pizzalist : [...pizzalist]
        }
    }

    pizzaEdit(element, index){

        this.setState({
            pizzaConfElement : {...element},
            pizzaElementChangeIndex : index,
            configuring : !(this.state.configuring)
        });
    }

    exitConfig(){
        this.setState({
            configuring : false
        })
    }

    editPizzaElement(element,index){
        console.log(index)
        this.setState(
            (state) =>
            {
                state.pizzalist[index] = element
            }
        )
    }

    configurable(configurable) {
        if(configurable){
            return (
                <Pizzaconf
                    editPizzaElement={this.editPizzaElement.bind(this)}
                    exitConfig={this.exitConfig.bind(this)}
                    pizzaElement={this.state.pizzaConfElement}
                    pizzaElementChangeIndex={this.state.pizzaElementChangeIndex}
                />)
        }else{
            return (
                <Pizzalist
                    pizzaList={this.state.pizzalist}
                    pizzaEdit={this.pizzaEdit.bind(this)}
                />)
        }
    }

    render() {
        return(
            this.configurable(this.state.configuring)
        )
    }
}

export default PizzaListConfig;