import React,{Component} from "react";
import Pizzaitem from "./pizzaitem";

class Pizzalist extends  Component{
    constructor(props) {
        super(props);
        this.state = {
            pizzaList : [...this.props.pizzaList]
        }
    }



    render() {
        return(
            <div className="container align-items-start form-row no-gutters">
                {this.state.pizzaList.map(
                    (element,index) => {
                        return(
                            <Pizzaitem
                                key={index}
                                index={index}
                                pizzaElement={element}
                                pizzaEdit={this.props.pizzaEdit}
                                pizzaConfPath="/pizzaconf"
                            />
                        )
                    }
                )}
            </div>
        )
    }
}

export default Pizzalist;