import React,{Component} from "react";
import Pizzaitem from "./pizzaitem";

class Pizzaconf extends Component{
    constructor(props) {
        super(props);
        this.state = {
            pizzaElement : this.props.pizzaElement
        }
    }

    changeName(value){
        this.setState((state)=> state.pizzaElement.name = value )
    }

    changeDescription(value){
        this.setState((state)=> state.pizzaElement.description = value )
    }

    changePrice(value){
        this.setState((state)=> state.pizzaElement.price = value )
    }

    render() {
        return(
            <div className="container d-flex mt-3 row no-gutters align-items-center text-center">
                <div className="col-lg-4 d-flex flex-column">
                    <label
                        className="mt-3"
                        htmlFor="PizzaNameEditInput">
                        Pizza name
                    </label>
                    
                    <input
                        onChange={(element)=>this.changeName(element.target.value)}
                        id="PizzaNameEditInput"
                        type="text"
                    />

                    <label
                        className="mt-3"
                        htmlFor="PizzaImageEditInput">
                        Pizza image
                    </label>

                    <input
                        id="PizzaImageEditInput"
                        type="file"
                    />

                    <label
                        className="mt-3"
                        htmlFor="PizzaDescriptionEditInput">
                        Pizza description
                    </label>

                    <input
                        onChange={(element)=>this.changeDescription(element.target.value)}
                        id="PizzaDescriptionEditInput"
                        type="text"
                    />

                    <label
                        className="mt-3"
                        htmlFor="PizzaPriceEditInput">
                        Pizza price
                    </label>

                    <input
                        onChange={(element)=>this.changePrice(element.target.value)}
                        id="PizzaPriceEditInput"
                        type="number"
                    />

                    <button
                        onClick={()=>this.props.editPizzaElement(this.state.pizzaElement, this.props.pizzaElementChangeIndex)}
                        className="btn btn-outline-success mt-3">
                        Submit editions
                    </button>

                    <button
                        onClick={this.props.exitConfig}
                        className="btn btn-outline-success mt-3">
                        Back
                    </button>
                </div>
                <Pizzaitem pizzaElement={this.props.pizzaElement}/>
            </div>
        )
    }
}

export default Pizzaconf;