import React,{Component} from "react";
import logo from "../img/1.png";
import {Link} from "react-router-dom";

class Pizzaitem extends Component{



    render() {
        return(
            <div className="col-lg-4 card m-auto">
                <h3 className="text-center">{this.props.pizzaElement.name}</h3>
                <img className="border card-img-top" src={logo} alt={`${this.props.pizzaElement.imageURL} picture`}/>
                <div className="card-body row">
                    <div className="col-auto">
                        <h4 className="card-title">Description</h4>
                        <p className="card-text">{this.props.pizzaElement.description}</p>
                    </div>
                    <div className="col-auto m-auto text-center">
                        <h4>Price</h4>
                        <h5 className="">{this.props.pizzaElement.price}$</h5>
                    </div>
                </div>
                <button onClick={()=>this.props.pizzaEdit(this.props.pizzaElement, this.props.index)} className="btn btn-outline-success">
                    Edit
                </button>
            </div>
        )
    }
}

export default Pizzaitem;
